@font-face {
  font-family: "GraduateRegular";
  src: local("GraduateRegular"),
    url("./Assets/fonts/Graduate-Regular.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Glory";
  src: local("GloryTSI-Regular"),
    url("./Assets/fonts/GloryTSI-Regular.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Frygia";
  src: local("Frygia"),
    url("./Assets/fonts/Frygia.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Fredoka";
  src: local("FredokaOne-Regular"),
    url("./Assets/fonts/FredokaOne-Regular.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Gurajada";
  src: local("Gurajada-Regular"),
    url("./Assets/fonts/Gurajada-Regular.ttf") format("truetype");
  font-weight: bold;
}

body {
  margin: auto;
  background-color: #19232E;
  /* margin: center, */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.root {
  text-align: center;
  margin-top: 2.5%;
  margin-bottom: 2.5%;
}

.about-me {
  text-align: center;
}

.about-me img {
  width: 250px;
  height: 250px;
  border-radius: 360px;
  border-style: solid;
  border-width: 3.5px;
  border-color: #683636;
}

.about-me .name {
  margin-top: 25px;
  color: #AB3737;
  font-family: "GraduateRegular";
  font-size: 40px;
}

.about-me .mail {
  margin-top: 15px;
  color: #D0D2D6;
  font-family: "Glory";
  font-size: 24px;
}

.about-me .job {
  margin-top: 15px;
  margin-bottom: 15px;
  color: #D0D2D6;
  font-family: "Glory";
  font-size: 24px;
}

.line {
  border-top: 0.25px solid white;
  width: 10.25%;
  margin: auto;
  margin-top: 5px;
  margin-bottom: 20px;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Icons */
.icons {
  margin-top: 40px;
  margin-bottom: 40px;
}

.icons .main-text {
  font-family: "Frygia";
  color: #7E7E7E;
  font-size: 22px;
  margin-top: 15px;
  margin-bottom: 10px;
}

.icons .logos a {
  color: initial;
  text-decoration: none;
}

.icons .logos img {
  margin-inline: 10px;
}

/* Tabs */

.tab {
  margin-top: 50px;
}

.tab .info {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}

.tab .info .name {
  padding-left: 20px;
  font-family: "Fredoka";
  color: #7E7E7E;
  font-size: 32px;
}

.tab .list {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  /* max-width: 1000px; */
  display: inline-grid;
  grid-template-columns: auto auto;
}

/* Link Blocks */
.link-block {
  text-align: center;
  display: inline-block;
  width: 300px;
  padding: .8em;
  padding-inline: 4.5em;
}

.link-block .logo img {
  width: 270px;
  height: 220px;
  border-radius: 8px;
  border-style: solid;
  border-width: 3px;
  border-color: #783939;
}

.link-block .main {
  text-align: center;
  display: flex;
  justify-content: center;
}

.link-block .main .name {
  color: #258E75;
  text-align: center;
}

.link-block .main .game-jam {
  color: #4B86A7;
  margin-left: 5px;
}

.link-block .main .name, .link-block .main .game-jam {
  font-family: "Gurajada";
  line-height: 30px;
  font-size: 29px;
  margin-top: 2px;
}

.link-block .description {
  font-family: "Frygia";
  color: #7a7979;
  font-size: 22px;
  margin-top: 6px;
  margin-bottom: 6px;
}

.link-block .links {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.link-block .links a {
  color: initial;
  text-decoration: none;
  font-family: "Frygia";
  color: #AB3737;
  font-size: 22px;
  margin-left: 5px;
  margin-top: 3px;
}

.link-block .links a:hover {
  text-decoration: underline;
}